import RouteBreadCrumb from '@/components/argon-core/Breadcrumb/RouteBreadcrumb'
import MembersTable from '~/components/shack/ArchivedInvitesTable.vue'
import MainTitle from '~/components/shack/MainTitle.vue'
import SectionTitle from '~/components/shack/SectionTitle.vue'
import SearchForm from '~/components/shack/SearchForm.vue'
import { getQueryParams } from '../../../../util/url'

export default {
  layout: 'ShackDash',
  async asyncData({ $membership, $plan, error, route }) {
    return await $membership
      .getArchivedInvites()
      .then(
        ({
          data,
          total,
          per_page,
          current_page,
          next_page_url,
          prev_page_url
        }) => {
          return {
            members: data,
            links: {
              next: next_page_url,
              prev: prev_page_url
            },
            meta: {
              total,
              per_page,
              current_page
            }
          }
        }
      )
      .catch(e => {
        error({
          statusCode: e.statusCode,
          message: e.response
            ? JSON.stringify(e.response.data.errors)
            : e.messge
        })
      })
  },
  components: {
    MainTitle,
    SearchForm,
    SectionTitle,
    MembersTable,
    RouteBreadCrumb
  },
  data: () => ({
    searchTerm: '',
    loading: false
  }),
  methods: {
    next() {
      const { next } = this.links
      const params = getQueryParams(next)

      this.$router.push(params)
      location.href = location.origin + this.$route.path + params
    },
    prev() {
      const { prev } = this.links
      const params = getQueryParams(prev)

      this.$router.push(params)
      location.href = location.origin + this.$route.path + params
    },
    changePage(pageNumber) {
      const params = `?page=${pageNumber}`
      this.$router.push(params)
      location.href = location.origin + this.$route.path + params
    },
    confirmMember(e) {
      this.$membership
        .inviteMember(e.id)
        .then(res => {
          this.$bvToast.toast('Member invited successfully', {
            variant: 'success'
          })
          setTimeout(() => {
            location.reload()
          }, 5000)
        })
        .catch(e => {
          this.$bvToast.toast(
            e.response ? JSON.stringify(e.response.data.errors) : e.message,
            {
              title: 'Error',
              variant: 'danger'
            }
          )
        })
    },
    inviteAllPendingMembers() {
      this.$membership
        .inviteAllPendingMembers()
        .then(res => {
          this.$bvToast.toast('Members invited successfully', {
            variant: 'success'
          })
          setTimeout(() => {
            location.reload()
          }, 5000)
        })
        .catch(e => {
          this.$bvToast.toast(
            e.response ? JSON.stringify(e.response.data.errors) : e.message,
            {
              title: 'Error',
              variant: 'danger'
            }
          )
        })
      console.log('invite all')
    },
    cancelMember(e) {
      if (!confirm('Are you sure')) return

      this.$membership
        .deleteMembership(e.id)
        .then(res => {
          this.$bvToast.toast('Member deleted successfully', {
            variant: 'success'
          })
          setTimeout(() => {
            location.reload()
          }, 5000)
        })
        .catch(e => {
          this.$bvToast.toast(
            e.response ? JSON.stringify(e.response.data.errors) : e.message,
            {
              title: 'Error',
              variant: 'danger'
            }
          )
        })
    },
    deleteArchivedInvitation(e) {
      if (!confirm('Are you sure')) return

      this.$membership
        .deleteArchivedInvite(e.id)
        .then(res => {
          this.$bvToast.toast('Invitation deleted successfully', {
            variant: 'success'
          })
          location.reload()
        })
        .catch(e => {
          this.$bvToast.toast(
            e.response ? JSON.stringify(e.response.data.errors) : e.message,
            {
              title: 'Error',
              variant: 'danger'
            }
          )
        })
    },
    search(query) {
      this.loading = !this.loading
      const link = `filter[search]=${query}`
      this.$membership
        .getArchivedInvites(link)
        .then(
          ({
            data,
            total,
            per_page,
            current_page,
            next_page_url,
            prev_page_url
          }) => {
            this.members = data
            this.links = {
              next: next_page_url,
              prev: prev_page_url
            }
            //this.meta = meta

            this.loading = false
          }
        )
        .catch(e => {
          this.loading = !this.loading
          this.$bvToast.toast(
            e.response ? JSON.stringify(e.response.data.errors) : e.message,
            {
              title: 'Error',
              variant: 'danger'
            }
          )
        })
    }
  }
}
